$accent-black-color: #000;
$accent-light-gray-color: #ddd;

$stopwatch-radius: 40vh;
$stopwatch-border-width: 0.25vh;
$stopwatch-center-radius: $stopwatch-radius * 0.05;
$stopwatch-minute-hand-length: $stopwatch-radius * 0.7;
$stopwatch-minute-hand-width: $stopwatch-center-radius * 0.5;
$stopwatch-second-hand-length: $stopwatch-radius * 0.9;
$stopwatch-second-hand-width: $stopwatch-center-radius * 0.1;
